/*    Reset CSS  */
* { margin:0;  padding:0;}
ol,ul { list-style: none; }
a { outline:0; text-decoration:none; color: black; text-decoration: none;}
a:visited, a:focus, a:link, a:active, a:hover {
    color: black;
    text-decoration: none;
}
a:hover {
    cursor: pointer;
}
img { border:0;}
h1 { margin: 0; }
hr {
  margin-top: 0;
  margin-bottom: 0;
}
html { height: 100%; }
